body, main, h1, h2,h3{
  padding: 0;
  margin: 0;
}
.container{
  max-width: 1440px;
  padding: 0 56px;
  width: 100%;
  position: relative;
  margin: 0 auto;
}
header{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.nav{
  display: flex;
}
.nav li{
  margin-left: 40px;
  font-family: 'Anonymous Pro';
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  text-decoration: none;
  list-style-type: none;
  cursor: pointer;
  transition: 0.3s ease;
}
.nav li:hover{
  color: #DC0000;
}
.line{
  width: 100%;
  height: 2px;
  background-color: black;
}
.main__img img{
  width: 100%;
  padding: 24px 0  56px  0;
}
.info{
  display: flex;
}
.content{
  width: 50%;
}
.name{
  font-family: 'Anonymous Pro';
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  /* identical to box height, or 150% */
  padding: 32px 0;
  letter-spacing: 0.02em;

  color: #000000;
}
.description{
  font-family: 'Anonymous Pro';
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  /* or 150% */
  margin-bottom: 32px;
  letter-spacing: 0.02em;

  color: #5C5C5C;
}
.button__name{
  font-family: 'Anonymous Pro';
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  /* identical to box height, or 150% */

  letter-spacing: 0.02em;
  text-decoration-line: underline;

  color: #000000;
  margin-bottom: 56px;
}


